import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'percentRate'
})
export class PercentRatePipe implements PipeTransform {

    transform(value: number): string {
        if (value % 1 !== 0) {
            return value.toFixed(2) + '%';
        }
        return value + '%';
    }
}



