import {
    Component, Input, Output, EventEmitter, HostBinding, ElementRef, ViewChild,
    AfterViewChecked
} from '@angular/core';

@Component({
    selector: '[custom-select]',
    templateUrl: 'custom-select.component.html'
})

export class CustomSelectComponent implements AfterViewChecked {

    options: Array<any> = [];

    @Input() id: string;

    @Input() value: any;
    @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();

    @Input() @HostBinding('style.width') elemWidth: string = '200px';
    @Input() @HostBinding('class.add-options') addOptions: boolean = false;
    @Input() @HostBinding('class.expand-up') expandUp: boolean = false;

    @HostBinding('class.input-text-shown') showInput: boolean = false;

    @ViewChild('select') select: ElementRef;
    @ViewChild('input') input: ElementRef;

    /**
     * Show/hide input
     */
    toggleInput() {
        this.showInput = !this.showInput;
    }

    /**
     * Focus on input when added
     */
    ngAfterViewChecked() {
        if (this.input) {
            this.input.nativeElement.focus();
        }
    }

    /**
     * Watch 'keydown' event
     * @param event
     */
    postOption(event: KeyboardEvent) {
        const value = this.input.nativeElement.value;

        if (event.keyCode === 13) {
            if (value) {
                this.options = [{label: value, value: value}].concat(this.options);
                this.valueChange.emit(value);
                this.input.nativeElement.blur();
            }
        }

        if (event.keyCode === 8 && !value) {
            this.input.nativeElement.blur();
        }
    }

    /**
     * Hide input text
     */
    hideInput() {
        this.showInput = false;
    }

    /**
     * Output chosen value
     * @param value
     */
    outputValue(value: any) {
        this.valueChange.emit(value);
        this.select.nativeElement.blur();
    }
}



