import {ModuleWithProviders} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AdminComponent} from './components/admin/admin.component';
import {DashboardComponent} from './components/admin/dashboard/dashboard.component';
import {ScrapersComponent} from './components/admin/scrapers/scrapers.component';
import {SettingsComponent} from './components/admin/settings/settings.component';
import {BusinessesComponent} from './components/admin/businesses/businesses.component';
import {QueueComponent} from './components/admin/queue/queue.component';
import {MailingComponent} from './components/admin/mailing/mailing.component';
import {UsersComponent} from './components/admin/users/users.component';
import {CompareScrapersComponent} from './components/admin/scrapers/compare-scrapers/compare-scrapers.component';
import {ScraperStatisticComponent} from './components/admin/scrapers/scraper-statistic/scraper-statistic.component';
import {ScraperTargetsComponent} from './components/admin/scrapers/scraper-targets/scraper-targets.component';
import {ScraperSitePreviewComponent} from './components/admin/scrapers/scraper-site-preview/scraper-site-preview.component';
import {SignInComponent} from './components/sign-in/sign-in.component';
import {SignedInGuard} from './guards/signed-in.guard';
import {SignedOutGuard} from './guards/signed-out.guard';
import {BusinessDetailsComponent} from './components/admin/businesses/single-business/business-details/business-details.component';
import {BusinessDesignsComponent} from './components/admin/businesses/single-business/business-designs/business-designs.component';
import {BusinessInteractionsComponent} from './components/admin/businesses/single-business/business-interactions/business-interactions.component';
import {SingleBusinessComponent} from './components/admin/businesses/single-business/single-business.component';

const appRoutes: Routes = [
    { path: '', redirectTo: 'admin/dashboard', pathMatch: 'full' },
    { path: 'sign-in', component: SignInComponent, canActivate: [SignedOutGuard] },
    {
        path: 'admin', component: AdminComponent, canActivate: [SignedInGuard], children: [
            { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
            { path: 'dashboard', component: DashboardComponent },
            { path: 'scrapers', component: ScrapersComponent},
            { path: 'scrapers/statistic/:version', component: ScraperStatisticComponent },
            { path: 'scrapers/targets/:version', component: ScraperTargetsComponent },
            { path: 'scrapers/compare/:scrap-left/:scrap-right', component: CompareScrapersComponent },
            { path: 'businesses', component: BusinessesComponent },
            { path: 'businesses/:id', component: SingleBusinessComponent, children: [
                { path: '', redirectTo: 'details', pathMatch: 'full' },
                { path: 'designs', component: BusinessDesignsComponent },
                { path: 'details', component: BusinessDetailsComponent },
                { path: 'interactions', component: BusinessInteractionsComponent },
            ]},
            { path: 'queue', component: QueueComponent },
            { path: 'mailing', component: MailingComponent },
            { path: 'users', component: UsersComponent },
            { path: 'settings', component: SettingsComponent }
        ]
    },
    { path: 'site-preview/:id', component: ScraperSitePreviewComponent, canActivate: [SignedInGuard] }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);



