import {Component, Input, OnInit} from '@angular/core';
import {Scraper} from '../../../../interfaces/scraper';
import {ScraperService} from '../../../../services/scraper/scraper.service';
import {BaseComponent} from '../../../base/base.component';

@Component({
    selector: '[scrapers-table-row]',
    templateUrl: 'scrapers-table-row.component.html'
})
export class ScrapersTableRowComponent extends BaseComponent implements OnInit {

    @Input() scraper: Scraper;

    isChecked: boolean = false;

    constructor(private _scrap: ScraperService) {
        super();
    }

    /**
     * Subscribe to selected items array
     */
    ngOnInit() {
        this._scrap.selectedForComparison.takeWhile(() => this.isAlive).subscribe(
            res => {
                this.isChecked = res.indexOf(this.scraper.version) > -1;
            }
        );
    }

    /**
     * Toggle selection
     */
    toggleChecked(): void {
        this.isChecked ? this._scrap.removeSelected(this.scraper.version) : this._scrap.addSelected(this.scraper.version);
    }
}



