import {Component, OnInit} from '@angular/core';
import {BusinessService} from '../../../services/business/business.service';
import {Business} from '../../../interfaces/business';
import {ModalService} from '../../../services/modal/modal.service';
import {BaseComponent} from '../../base/base.component';

@Component({
    selector: 'businesses',
    templateUrl: 'businesses.component.html'
})
export class BusinessesComponent extends BaseComponent implements OnInit {

    someValue: number = 27; // TODO temp

    businesses: Array<Business> = [];

    selectedForScraping: Array<number> = [];

    showFilters: boolean = false;

    searchQuery: string = '';

    currentPage: number = 1;
    totalPages: number = null;

    /**
     * currentDataset get/set
     * @type {number}
     * @private
     */
    private _currentDataset: number = 0;

    get currentDataset(): number {
        return this._currentDataset;
    }

    set currentDataset(value: number) {
        this._currentDataset = value;
        this.currentPage = 1;
        this.getBusinesses();
    }

    /**
     * itemsPerPage get/set
     * @type {number}
     * @private
     */
    private _itemsPerPage: number = 10;

    get itemsPerPage(): number {
        return this._itemsPerPage;
    }

    set itemsPerPage(value: number) {
        this._itemsPerPage = value;
        this.currentPage = 1;
        this.getBusinesses();
    }

    constructor(private _busin: BusinessService,
                private _modal: ModalService) {
        super();
    }

    /**
     * Get businesses by default params
     */
    ngOnInit() {
        this.getBusinesses();
        this._busin.selectedForScraping.takeWhile(() => this.isAlive).subscribe(
            res => {
                this.selectedForScraping = res;
            }
        );
    }

    /**
     * Get businesses as per current params set
     */
    getBusinesses(): void {
        const args = {
            dataset: this.currentDataset,
            currPage: this.currentPage,
            perPage: this.itemsPerPage,
            search: this.searchQuery
        };
        this._busin.getBusinesses(args).subscribe(
            res => {
                this.businesses = res.data;
                this.totalPages = res.pagination.page_count;
                console.log(res);
            }
        );
    }

    /**
     * Toggle filters on/off
     */
    toggleFilters(): void {
        this.showFilters = !this.showFilters;
    }

    /**
     * Update current page and get sites
     * @param page
     */
    goToPage(page: number) {
        this.currentPage = page;
        this.getBusinesses();
    }

    /**
     * Update current page and get businesses
     * @param perPage
     */
    updatePerPage(perPage: number) {
        this.currentPage = 1;
        this.itemsPerPage = perPage;
        this.getBusinesses();
    }

    /**
     * Set search query and get businesses
     * @param query
     */
    setSearchQuery(query: string): void {
        this.currentPage = 1;
        this.searchQuery = query;
        this.getBusinesses();
    }

    /**
     * Set current dataset and get businesses
     * @param dataset
     */
    setDataset(dataset: number) {
        this.currentDataset = dataset;
        this.getBusinesses();
    }

    /**
     * Open upload dataset modal
     */
    uploadDataset() {
        this._modal.openUploadDataset();
    }

    /**
     * Scrap selected items
     */
    scrapSelected(): void {
        this._busin.scrapSelected();
    }

    /**
     * Clear all selected businesses
     */
    clearSelected(): void {
        this._busin.clearSelection();
    }
}



