import {Injectable} from '@angular/core';
import {ApiService} from '../api/api.service';
import {Observable} from 'rxjs/Observable';
import {BusinessesQueryArgs} from '../../interfaces/businesses-query-args';
import {Site} from '../../interfaces/site';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class BusinessService {

    currentScrapUnderReview: BehaviorSubject<Site> = new BehaviorSubject<Site>(null);

    selectedForScraping: BehaviorSubject<Array<any>> = new BehaviorSubject<Array<any>>([]);

    constructor(private _api: ApiService) {
    }

    /**
     * Get all businesses
     * @returns {Observable<R>}
     */
    getBusinesses(args: BusinessesQueryArgs): Observable<any> {
        const reqUrl = 'firms?dataset_id=' + (args.dataset && args.dataset !== '0' ? args.dataset : '') +
                       '&page=' + args.currPage +
                       '&per_page=' + args.perPage +
                       '&q=' + args.search;
        return this._api.get(reqUrl);
    }

    /**
     * Get last scraper result for business
     * @param id
     * @returns {Observable<R>}
     */
    getLastBusinessScrap(id: number): Observable<any> {
        return this._api.get(`firms/${id}/sites/last`);
    }

    /**
     * Set currently reviewed business scraping result
     * @param site
     */
    setCurrentScrapUnderReview(site: Site): void {
        this.currentScrapUnderReview.next(site);
    }

    /**
     * * Clear currently reviewed business scraping result
     */
    clearCurrentScrapUnderReview(): void {
        this.currentScrapUnderReview.next(null);
    }

    /**
     * Get businesses selected for scraping
     * @returns {Array<any>}
     */
    getSelected(): Array<any> {
        return this.selectedForScraping.getValue();
    }

    /**
     * Add selected business to selected list
     * @param version
     */
    addSelected(id: number): void {
        this.selectedForScraping.next(this.getSelected().concat(id));
    }

    /**
     * Remove selected scraper from selected list
     * @param version
     */
    removeSelected(id: number): void {
        const selected = this.getSelected();
        const index = selected.indexOf(id);
        if (index > -1) {
            selected.splice(index, 1);
            this.selectedForScraping.next(selected);
        }
    }

    /**
     * Scrap selected items // TODO: temp
     */
    scrapSelected(): void {
        console.log(this.getSelected());
    }

    /**
     * Clear selected scrapers info
     */
    clearSelection(): void {
        this.selectedForScraping.next([]);
    }
}



