import {Injectable} from '@angular/core';
import {Router} from '@angular/router';
import {environment as env} from '../../../environments/environment';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import 'rxjs/add/operator/map';
import {ApiService} from '../api/api.service';
import {Subject} from 'rxjs/Subject';
import {ErrorService} from '../error/error.service';

@Injectable()
export class AuthService {

    storageKey: string = env.AUTH_TOKEN_STORAGE_KEY;

    currentUser: Subject<string> = new BehaviorSubject<string>(null);

    constructor(private _api: ApiService,
                private _router: Router,
                private _err: ErrorService) {
        this._err.shoutSignOut.subscribe(res => {
            this.signOut();
        });
        if (this.isSignedIn()) {
            this.verifyCurrentUser();
        }
    }

    /**
     * Check if user is signed in
     * @returns {boolean}
     */
    isSignedIn(): boolean {
        return this.getToken() !== null;
    }

    /**
     * Authorize user with provided credentials
     * @param email
     * @param password
     */
    authorize(email: string, password: string): void {
        this._api.post('access', {email: email, password: password}).subscribe(
            res => {
                this.setToken(res.auth_token);
                this.currentUser.next(email);
                this._router.navigate(['']);
            }
        );
    }

    /**
     * Set auth token to local storage
     * @param token
     */
    setToken(token: string): void {
        localStorage.setItem(this.storageKey, token);
    }

    /**
     * Get auth token from local storage
     * @returns {string|null}
     */
    getToken(): string {
        return localStorage.getItem(this.storageKey);
    }

    /**
     * Verify current user
     */
    verifyCurrentUser(): void {
        this._api.get('access/current_admin').subscribe(
            res => {
                this.currentUser.next(res.current_admin.email);
            },
            err => {
                this.signOut();
            }
        );
    }

    /**
     * Sign out current user
     */
    signOut(): void {
        localStorage.removeItem(this.storageKey);
        this.currentUser.next(null);
        this._router.navigate(['sign-in']);
    }
}



