import {Injectable} from '@angular/core';
import {Subject} from 'rxjs/Subject';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Modal} from '../../interfaces/modal';

@Injectable()
export class ModalService {

    modal: Subject<Modal> = new BehaviorSubject<Modal>(null);

    /**
     * Show success modal
     * @param success
     */
    showSuccess(success: string) {
        this.showModal({
            type: 'success',
            message: success
        });
    }

    /**
     * Show error modal
     * @param error
     */
    showError(error: string) {
        this.showModal({
            type: 'error',
            message: error
        });
    }

    /**
     * Open asset fail reason modal
     * @param siteId
     * @param assetType
     */
    openAssetFailReason(siteId: number, assetType: string, feedbackText?: string) {
        this.showModal({
            type: 'asset-fail-reason',
            siteId,
            assetType,
            feedbackText: feedbackText || null
        });
    }

    /**
     * Open upload dataset modal
     */
    openUploadDataset() {
        this.showModal({
            type: 'upload-dataset'
        });
    }

    /**
     * Display modal
     * @param modal
     */
    showModal(modal: Modal) {
        this.modal.next(modal);
    }

    /**
     * Close modal
     */
    closeModal() {
        this.modal.next(null);
    }
}



