import {Component, Output, EventEmitter} from '@angular/core';
import {CustomSelectComponent} from '../custom-select.component';

@Component({
    selector: '[select-missing-asset]',
    templateUrl: 'select-missing-asset.component.html'
})
export class SelectMissingAssetComponent extends CustomSelectComponent {

    @Output() remove: EventEmitter<any> = new EventEmitter<any>();

    options: Array<any> = [
        { label: 'Missing Asset', value: ''},
        { label: 'Company Name', value: 'name'},
        { label: 'Slogan', value: 'slogan'},
        { label: 'Phone', value: 'phone'},
        { label: 'Colors', value: 'colors'},
        { label: 'Font', value: 'font'},
        { label: 'Logo', value: 'logo'},
        { label: 'Images', value: 'backgrounds'}
    ];

    constructor() {
        super();
    }

    /**
     * Emit remove picker event
     */
    removeThisPicker() {
        this.remove.emit();
    }
}



