import {Component, OnInit} from '@angular/core';
import Chart from 'chart.js';

@Component({
    selector: 'dashboard',
    templateUrl: 'dashboard.component.html'
})
export class DashboardComponent implements OnInit {

    options: Array<Object> = [
        {title: 'One', value: 1},
        {title: 'Two', value: 2},
        {title: 'Three', value: 3},
        {title: 'Four', value: 4},
        {title: 'this text is yuuuuuge and you should think of fitting it inside the div', value: 5}
    ];

    constructor() {
    }

    ngOnInit() {
        this.initBusinessesPieChart();
        this.initResponseBarChart();
    }

    initBusinessesPieChart() {
        const businessesCanvas: HTMLCanvasElement = <HTMLCanvasElement>document.getElementById('businesses-pie-chart');
        const ctx = businessesCanvas.getContext('2d');
        const businessesChart = new Chart(ctx, {
            type: 'pie',
            data: {
                datasets: [{
                    label: 'Population (millions)',
                    backgroundColor: ['#5190fb', '#edeef2'],
                    data: [ 67, 33 ]
                }]
            },
            options: {
                tooltips: {
                    enabled: false
                },
                maintainAspectRatio: false
            }
        });
    }

    initResponseBarChart() {

        const responseCanvas: HTMLCanvasElement = <HTMLCanvasElement>document.getElementById('response-bar-chart');
        const ctx = responseCanvas.getContext('2d');
        const responseChart = new Chart(ctx, {
            type: 'bar',
            data: {
                labels: ['Emailed', 'Link Clicked', 'Form Started', 'Form Submitted', 'Unsubscribed'],
                datasets: [{
                    data: [ 5767, 4678, 256, 253, 254 ],
                    backgroundColor: [
                        '#edeef2',
                        '#edeef2',
                        '#edeef2',
                        'rgba(75, 192, 192, 0.2)',
                        '#edeef2'
                    ]
                }]
            },
            options: {
                legend: {
                    display: false
                },
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: true,
                            callback: function(value) {
                                return value >= 1000 ? value / 1000 + 'K' : value;
                            }
                        }
                    }],
                    xAxes: [{
                        barThickness: 75
                    }]
                },
                tooltips: {
                    enabled: false
                },
                maintainAspectRatio: false
            }
        });
    }
}



