import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {SiteService} from '../../../../services/site/site.service';
import {Site} from '../../../../interfaces/site';
import {BaseComponent} from '../../../base/base.component';
import 'rxjs/add/operator/takeWhile';

@Component({
    selector: 'scraper-site-preview',
    templateUrl: 'scraper-site-preview.component.html'
})
export class ScraperSitePreviewComponent extends BaseComponent implements OnInit {

    siteId: number = null;

    site: Site = null;
    prevSite: Site = null;
    nextSite: Site = null;

    constructor(private _ar: ActivatedRoute,
                private _site: SiteService) {
        super();
    }

    /**
     * Get site id from route params
     * Get site by id
     */
    ngOnInit() {
        this._ar.params.subscribe(params => {
            this.siteId = params.id;
            this.getSiteInfo();
        });
        this._site.feedbackAdded.takeWhile(() => this.isAlive).subscribe(
            res => {
                if (res) {
                    this.getSiteInfo();
                }
            }
        );
    }

    /**
     * Get site info call
     */
    getSiteInfo() {
        this._site.getSiteById(this.siteId).subscribe(
            res => {
                this.site = res.site;
                const index = this._site.currentSitesSet.map((site) => {
                    return site.id;
                }).indexOf(this.site.id);
                this.prevSite = this._site.currentSitesSet[index - 1] || null;
                this.nextSite = this._site.currentSitesSet[index + 1] || null;
                console.log(res);
            }
        );
    }

    /**
     * Set status for the site
     * @param status
     */
    setStatus(status: string) {
        this._site.updateSiteStatus(this.siteId, status).subscribe(
            res => { this.getSiteInfo(); }
        );
    }
}



