import {Component, Input, OnChanges, SimpleChanges} from '@angular/core';
import {ScraperService} from '../../../../../services/scraper/scraper.service';
import {Scraper} from '../../../../../interfaces/scraper';

@Component({
    selector: '[scraper-compared]',
    templateUrl: 'scraper-compared.component.html'
})
export class ScraperComparedComponent implements OnChanges {

    @Input() scraperVersion: number = null;
    @Input() currDataset: number = 0;
    @Input() slug: string = null;
    scraper: Scraper = null;

    constructor(private _scrap: ScraperService) {
    }

    ngOnChanges(changes: SimpleChanges) {
        this.getScraperInfo();
    }

    /**
     * Get compared scraper info
     */
    getScraperInfo(): void {
        this._scrap.getScraperByVersion(this.scraperVersion, this.currDataset).subscribe(
            res => {
                this.scraper = res;
            }
        );
    }
}



