import {Component, OnInit, HostBinding, Input} from '@angular/core';
import {CompleterService, CompleterData} from 'ng2-completer';

@Component({
    selector: 'auto-complete',
    templateUrl: 'auto-complete.component.html'
})
export class AutoCompleteComponent implements OnInit {

    @HostBinding('class.dropdown-shown') isOpened: boolean = false;
    @HostBinding('style.width') @Input() elemWidth: string = '250px';
    @Input() placeholder: string = 'Select or start typing';

    captain: string;
    captains = ['James T. Kirk', 'Benjamin Sisko', 'Jean-Luc Picard', 'Spock', 'Jonathan Archer', 'Hikaru Sulu', 'Christopher Pike', 'Rachel Garrett' ];

    constructor(private _compl: CompleterService) {
    }

    ngOnInit() {
    }

    setOpened(status: boolean) {
        this.isOpened = status;
    }
}



