import {Component, Input} from '@angular/core';
import {ModalService} from '../../services/modal/modal.service';

@Component({
    selector: '[asset-wrapper]',
    templateUrl: 'asset-wrapper.component.html'
})
export class AssetWrapperComponent {

    @Input() siteId: number;
    @Input() assetType: string;
    @Input() asset: any;
    @Input() assetFeedback: string = null;
    @Input() enlarged: false;

    constructor(private _modal: ModalService) {
    }

    /**
     * Open asset fail reason popup
     */
    postReason() {
        this._modal.openAssetFailReason(this.siteId, this.assetType, this.assetFeedback || null);
    }
}



