import {Component, OnInit} from '@angular/core';
import {Site} from '../../../../../interfaces/site';
import {BaseComponent} from '../../../../base/base.component';
import {BusinessService} from '../../../../../services/business/business.service';

@Component({
    selector: 'business-details',
    templateUrl: 'business-details.component.html'
})
export class BusinessDetailsComponent extends BaseComponent implements OnInit {

    site: Site = null;

    constructor(private _busin: BusinessService) {
        super();
    }

    ngOnInit() {
        this._busin.currentScrapUnderReview.takeWhile(() => this.isAlive).subscribe(
            res => {
                this.site = res;
            }
        );
    }
}



