import {Injectable} from '@angular/core';
import {ApiService} from '../api/api.service';
import {Observable} from 'rxjs/Observable';
import {Site} from '../../interfaces/site';
import {Subject} from 'rxjs/Subject';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {SitesQueryArgs} from '../../interfaces/sites-query-args';

@Injectable()
export class SiteService {

    currentSitesSet: Array<Site> = [];

    feedbackAdded: Subject<boolean> = new BehaviorSubject<boolean>(null);

    constructor(private _api: ApiService) {
    }

    /**
     * Get sites by scraper version
     * @param args
     * @returns {Observable<R>}
     */
    getSitesByScraper(args: SitesQueryArgs): Observable<any> {
        let reqUrl = 'sites?version=' + args.version +
                       '&page=' + args.currPage +
                       '&per_page=' + args.perPage +
                       (args.dataset && args.dataset !== '0' ? '&dataset_id=' + args.dataset : '') +
                       (args.search ? '&q=' + args.search : '') +
                       (args.successScraped ? '&successfully_scraped=true' : '') +
                       (args.withMissed ? '&with_missed_assets=true' : '') +
                       (args.withErrors ? '&with_errors==true' : '');
        if (args.byMissingAsset) {
            reqUrl += '&with_certain_missed_assets=' + args.byMissingAsset.toString();
        }
        return this._api.get(reqUrl);
    }

    /**
     * Get site by id
     * @param id
     * @returns {Observable<R>}
     */
    getSiteById(id: number): Observable<any> {
        return this._api.get(`sites/${id}`);
    }

    /**
     * Let subscribers know asset feedback is added
     */
    shoutFeedback() {
        this.feedbackAdded.next(true);
    }

    /**
     * Get asset fail reasons by type
     * @param assetType
     * @returns {Observable<R>}
     */
    getReasonsByAssetType(assetType: string): Observable<any> {
        return this._api.get(`reasons?asset_type=${assetType}`);
    }

    /**
     * Post asset fail reason
     * @param siteId
     * @param assetType
     * @param failReason
     * @returns {Observable<R>}
     */
    postAssetFailReason(siteId: number, assetType: string, failReason: string) {
        const reqBody = {};
        reqBody[assetType] = failReason;
        return this._api.post(`sites/${siteId}/feedback`, reqBody);
    }

    /**
     * Update site status
     * @param siteId
     * @param status
     * @returns {Observable<R|T>}
     */
    updateSiteStatus(siteId: number, status: string) {
        return this._api.put(`/sites/${siteId}`, {status});
    }
}



