import {Component, OnInit, OnDestroy} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Site} from '../../../../interfaces/site';
import {BusinessService} from '../../../../services/business/business.service';

@Component({
    selector: 'single-business',
    templateUrl: 'single-business.component.html'
})
export class SingleBusinessComponent implements OnInit, OnDestroy {

    businessId: number;
    lastScraped: Site = null;

    constructor(private _ar: ActivatedRoute,
                private _busin: BusinessService) {
    }

    /**
     * Get current business per route params
     * and store it in the service
     */
    ngOnInit() {
        this._ar.params.subscribe(params => {
            this.businessId = params.id;
            this._busin.getLastBusinessScrap(this.businessId).subscribe(
                res => {
                    console.log(res);
                    this.lastScraped = res.site;
                    this._busin.setCurrentScrapUnderReview(res.site);
                }
            );
        });
    }

    /**
     * Reset currently reviewed scrap
     */
    ngOnDestroy() {
        this._busin.clearCurrentScrapUnderReview();
    }
}



