import {Component} from '@angular/core';
import {CustomSelectComponent} from '../custom-select.component';

@Component({
    selector: '[select-per-page]',
    templateUrl: 'select-per-page.component.html'
})
export class SelectPerPageComponent extends CustomSelectComponent {

    options: Array<any> = [
        {label: '10 on page', value: 10},
        {label: '25 on page', value: 25},
        {label: '50 on page', value: 50}
    ];

    constructor() {
        super();
    }
}



