import {Component, OnInit} from '@angular/core';
import {ScraperService} from '../../../services/scraper/scraper.service';
import {CustomSelectComponent} from '../custom-select.component';

@Component({
    selector: '[select-scraper-version]',
    templateUrl: 'select-scraper-version.component.html'
})
export class SelectScraperVersionComponent extends CustomSelectComponent implements OnInit {

    options: Array<Object> = [];

    constructor(private _scrap: ScraperService) {
        super();
    }

    /**
     * Get available scraper versions
     * and setup picker
     */
    ngOnInit() {
        this._scrap.getScrapers().subscribe(
            res => {
                const versions = res.scrapers
                    .map(scraper => scraper.version)
                    .filter(version => version !== this.value);
                this.options = [{value: this.value, label: this.value}]
                    .concat(versions.map(version => { return {value: version, label: version }; }));
            }
        );
    }
}



