import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';

@Component({
    selector: 'compare-scrapers',
    templateUrl: 'compare-scrapers.component.html'
})
export class CompareScrapersComponent implements OnInit {

    scrapVersionLeft: number = null;
    scrapVersionRight: number = null;

    currentDataset: number = 0;

    constructor(private _ar: ActivatedRoute) {
    }

    /**
     * Get scraper versions from route params
     */
    ngOnInit(): void {
        this._ar.params.subscribe(params => {
            this.scrapVersionLeft = Number(params['scrap-left']);
            this.scrapVersionRight = Number(params['scrap-right']);
        });
    }
}



