import {Injectable} from '@angular/core';
import {Http, Headers, Request, RequestOptions, RequestMethod, Response} from '@angular/http';
import {environment as env} from '../../../environments/environment';
import {LogService} from '../log/log.service';
import {Observable} from 'rxjs/Rx';
import {ErrorService} from '../error/error.service';
import 'rxjs/add/operator/map';

@Injectable()
export class ApiService {

    constructor (private _http: Http,
                 private _log: LogService,
                 private _err: ErrorService) {
    }

    /**
     * GET request to API
     * @param url
     * @returns {Observable<R>}
     */
    get(url: string) {
        return this.request(url, RequestMethod.Get);
    }

    /**
     * POST request to API
     * @param url
     * @param body
     * @returns {Observable<R>}
     */
    post(url: string, body: Object) {
        return this.request(url, RequestMethod.Post, body);
    }

    /**
     * PUT request to API
     * @param url
     * @returns {Observable<R|T>}
     */
    put(url: string, body: Object) {
        return this.request(url, RequestMethod.Put, body);
    }

    /**
     * Upload request to API
     * @param url
     * @param data
     * @returns {Observable<R>}
     */
    upload(url: string, data: FormData) {
        return this.request(url, RequestMethod.Post, data, true);
    }

    /**
     * HTTP request template
     * @param url
     * @param method
     * @param body
     * @param upload
     * @returns {Observable<R|T>}
     */
    request(url: string, method: RequestMethod, body?: Object, upload: boolean = false) {

        const headers = new Headers();
        if (!upload) { headers.append('Content-Type', 'application/json'); }
        headers.append('Auth-Token', localStorage.getItem(env.AUTH_TOKEN_STORAGE_KEY));

        const requestOptions = new RequestOptions({
            url: `${env.API_URL}/${url}`,
            method: method,
            headers: headers
        });

        if (body) {
            requestOptions.body = body;
        }

        const request = new Request(requestOptions);

        return this._http.request(request).map((res: Response) => res.json())
            .catch((err: any) => {
                const errText = this._err.humanize(err);
                this._log.logError(errText);
                return Observable.throw(err);
            });
    }
}



