import {Component, OnInit} from '@angular/core';
import {CustomSelectComponent} from '../custom-select.component';
import {DatasetService} from '../../../services/dataset/dataset.service';

@Component({
    selector: '[select-data-set]',
    templateUrl: 'select-data-set.component.html'
})
export class SelectDataSetComponent extends CustomSelectComponent implements OnInit {

    options: Array<Object> = [];

    constructor(private _dset: DatasetService) {
        super();
    }

    /**
     * Get data sets options
     */
    ngOnInit() {
        this._dset.getDatasets().subscribe(
            res => {
                const datasetOptions = res.datasets.map((dataset) => {
                    return {value: dataset.id, label: dataset.title};
                });
                this.options = [{value: 0, label: 'All Data Sets'}].concat(datasetOptions);
            }
        );
    }
}



