import {Component, OnChanges, Input, SimpleChanges} from '@angular/core';
import {CustomSelectComponent} from '../../../components/custom-select/custom-select.component';
import {SiteService} from '../../../services/site/site.service';

@Component({
    selector: '[select-failed-asset-reason]',
    templateUrl: 'select-failed-asset-reason.component.html'
})
export class SelectFailedAssetReasonComponent extends CustomSelectComponent implements OnChanges {

    @Input() assetType: string = '';

    options: Array<any> = [
        { label: 'Fail Reason', value: ''}
    ];

    constructor(private _site: SiteService) {
        super();
    }

    /**
     * Update options when asset type is changed
     * @param changes
     */
    ngOnChanges(changes: SimpleChanges) {
        if (changes.assetType && !changes.assetType.firstChange) {
            this.updateOptions();
        }
    }

    /**
     * Update options based on asset type value
     */
    updateOptions(): void {
        if (this.assetType) {
            this._site.getReasonsByAssetType(this.assetType).subscribe(
                res => {
                    const reasons = res[this.assetType].map(reason => { return { label: reason, value: reason }; });
                    this.options = [{ label: 'Fail Reason', value: '' }].concat(reasons);
                }
            );
        } else {
            this.options = [{ label: 'Fail Reason', value: '' }];
        }
        this.value = '';
    }
}



