import {Component, OnInit} from '@angular/core';
import {ScraperService} from '../../../../services/scraper/scraper.service';
import {ActivatedRoute} from '@angular/router';
import {Scraper} from '../../../../interfaces/scraper';
import {SiteService} from '../../../../services/site/site.service';
import {Site} from '../../../../interfaces/site';
import {BaseComponent} from '../../../base/base.component';

@Component({
    selector: 'scraper-targets',
    templateUrl: 'scraper-targets.component.html'
})
export class ScraperTargetsComponent extends BaseComponent implements OnInit {

    scraper: Scraper;
    sites: Array<Site> = [];
    scraperVersion: number = null;

    showFilters: boolean = false;
    currentFilters: Object = null;

    searchQuery: string = '';

    currentPage: number = 1;
    totalPages: number = null;

    totalSites: number;

    missingAssetPickers: Array<any> = [];
    assetFailReasonPickers: Array<any> = [];

    /**
     * withErrors get/set
     * @type {boolean}
     * @private
     */
    private _withErrors: boolean = false;

    get withErrors(): boolean {
        return this._withErrors;
    }

    set withErrors(value: boolean) {
        if (value) {
            this.successfullyScraped = false;
        }
        this._withErrors = value;
    }

    /**
     * withMissedAssets get/set
     * @type {boolean}
     * @private
     */
    private _withMissedAssets: boolean = false;

    get withMissedAssets(): boolean {
        return this._withMissedAssets;
    }

    set withMissedAssets(value: boolean) {
        if (value) {
            this.successfullyScraped = false;
        }
        this._withMissedAssets = value;
    }

    /**
     * successfullyScraped get/set
     * @type {boolean}
     * @private
     */
    private _successfullyScraped: boolean = false;

    get successfullyScraped(): boolean {
        return this._successfullyScraped;
    }

    set successfullyScraped(value: boolean) {
        if (value) {
            this.withErrors = false;
            this.withMissedAssets = false;
            this.missingAssetPickers = [];
            this.assetFailReasonPickers = [];
        }
        this._successfullyScraped = value;
    }

    /**
     * currentDataset get/set
     * @type {number}
     * @private
     */
    private _currentDataset: number = 0;

    get currentDataset(): number {
        return this._currentDataset;
    }

    set currentDataset(value: number) {
        this._currentDataset = value;
        this.currentPage = 1;
        this.getSites();
    }

    /**
     * itemsPerPage get/set
     * @type {number}
     * @private
     */
    private _itemsPerPage: number = 10;

    get itemsPerPage(): number {
        return this._itemsPerPage;
    }

    set itemsPerPage(value: number) {
        this._itemsPerPage = value;
        this.currentPage = 1;
        this.getSites();
    }

    constructor(private _scrap: ScraperService,
                private _site: SiteService,
                private _ar: ActivatedRoute) {
        super();
    }

    /**
     * Get scraper by version per route param
     * Get sites by scraper version
     * Get sites as per search request change
     */
    ngOnInit(): void {
        this._ar.params.subscribe(params => {
            this.scraperVersion = params['version'];
            this._scrap.getScraperByVersion(params['version']).subscribe(
                res => {
                    this.scraper = res;
                    this.getSites();
                }
            );
        });
        this._site.feedbackAdded.takeWhile(() => this.isAlive).subscribe(
            res => {
                if (res) {
                    this.getSites();
                }
            }
        );
    }

    /**
     * Get sites as per current params
     */
    getSites() {
        let args = {
            version: this.scraperVersion,
            dataset: this.currentDataset,
            currPage: this.currentPage,
            perPage: this.itemsPerPage,
            search: this.searchQuery
        };
        if (this.currentFilters) {
            args = Object.assign(args, this.currentFilters);
        }
        this._site.getSitesByScraper(args).subscribe(
            res => {
                this.sites = res.data;
                this._site.currentSitesSet = res.data;
                this.totalPages = res.pagination.page_count;
                this.totalSites = res.pagination.pagination_record_count;
                console.log(res);
            }
        );
    }

    /**
     * Update current page and get sites
     * @param page
     */
    goToPage(page: number) {
        this.currentPage = page;
        this.getSites();
    }

    /**
     * Update current page and get sites
     * @param perPage
     */
    updatePerPage(perPage: string) {
        if (parseInt(perPage, 10) !== this.itemsPerPage) {
            this.currentPage = 1;
            this.itemsPerPage = parseInt(perPage, 10);
            this.getSites();
        }
    }

    /**
     * Toggle filters on/off
     */
    toggleFilters(): void {
        this.showFilters = !this.showFilters;
    }

    /**
     * Set search query and get sites
     * @param query
     */
    setSearchQuery(query: string): void {
        this.currentPage = 1;
        this.searchQuery = query;
        this.getSites();
    }

    /**
     * Add new missing asset filter
     */
    addMissingAssetFilter() {
        this.successfullyScraped = false;
        this.missingAssetPickers.push({
            id: Date.now(),
            assetType: ''
        });
    }

    /**
     * Remove missing asset filter
     * @param id
     */
    removeMissingAssetFilter(id: number) {
        const pickers = this.missingAssetPickers;
        for (let i = 0; i < pickers.length; i++) {
            if (pickers[i].id === id) {
                pickers.splice(i, 1);
            }
        }
    }

    /**
     * Set missing asset filter item
     * @param id
     * @param value
     */
    setMissingAssetItem(id: number, value: string) {
        const pickers = this.missingAssetPickers;
        for (let i = 0; i < pickers.length; i++) {
            if (pickers[i].id === id) {
                console.log(value);
                pickers[i]['assetType'] = value;
            }
        }
    }

    /**
     * Add asset fail reason picker
     */
    addAssetFailReasonFilter() {
        this.successfullyScraped = false;
        this.assetFailReasonPickers.push({
            id: Date.now(),
            assetType: '',
            assetFailReason: ''
        });
    }

    /**
     * Remove asset fail reason picker
     * @param id
     */
    removeAssetFailReasonFilter(id: number) {
        const pickers = this.assetFailReasonPickers;
        for (let i = 0; i < pickers.length; i++) {
            console.log(typeof pickers[i].id);
            console.log(typeof id);
            if (pickers[i].id === id) {
                pickers.splice(i, 1);
            }
        }
    }

    /**
     * Set fail reason array item
     * @param id
     * @param picker
     */
    setFailReasonItem(id: number, picker: any) {
        const pickers = this.assetFailReasonPickers;
        for (let i = 0; i < pickers.length; i++) {
            if (pickers[i].id === id) {
                pickers[i]['assetType'] = picker.assetType;
                pickers[i]['assetFailReason'] = picker.assetFailReason;
            }
        }
    }

    /**
     * Apply filters
     */
    applyFilters(): void {
        this.currentPage = 1;
        this.currentFilters = {
            withErrors: this.withErrors,
            withMissed: this.withMissedAssets,
            successScraped: this.successfullyScraped,
            byMissingAsset: this.missingAssetPickers.map(filter => filter.assetType),
            byAssetFailReason: this.assetFailReasonPickers.map(filter => filter)
        };
        this.getSites();
    }

    /**
     * Reset filters
     */
    resetFilters(): void {
        this.withErrors = false;
        this.withMissedAssets = false;
        this.successfullyScraped = false;
        this.missingAssetPickers = [];
        this.assetFailReasonPickers = [];
        this.currentFilters = null;
        this.currentPage = 1;
        this.getSites();
    }
}



