import {Injectable} from '@angular/core';
import {Subject} from 'rxjs/Subject';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class LoaderService {

    loaderGlobal: Subject<boolean> = new BehaviorSubject<boolean>(false);
    loaderAdmin: Subject<boolean> = new BehaviorSubject<boolean>(false);

    /**
     * Show global loader
     */
    showGlobal() {
        this.loaderGlobal.next(true);
    }

    /**
     * Hide global loader
     */
    hideGlobal() {
        this.loaderGlobal.next(false);
    }

    /**
     * Show admin loader
     */
    showAdmin() {
        this.loaderAdmin.next(true);
    }

    /**
     * Hide admin loader
     */
    hideAdmin() {
        this.loaderAdmin.next(false);
    }
}



