import {Component, OnInit} from '@angular/core';
import {ModalComponent} from '../modal.component';
import {ModalService} from '../../../services/modal/modal.service';
import {SiteService} from '../../../services/site/site.service';
import {LogService} from '../../../services/log/log.service';

@Component({
    selector: '[modal-fail-reason]',
    templateUrl: 'modal-fail-reason.component.html'
})
export class ModalFailReasonComponent extends ModalComponent implements OnInit {

    failReason: string = '';

    constructor(public _modal: ModalService,
                private _site: SiteService,
                private _log: LogService) {
        super(_modal);
    }

    /**
     * Set current fail reason value
     */
    ngOnInit() {
        this.failReason = this.modal.feedbackText || '';
    }


    /**
     * Post fail reason
     */
    postReason() {
        if (this.failReason) {
            this._site.postAssetFailReason(this.modal.siteId, this.modal.assetType, this.failReason).subscribe(
                res => {
                    this._log.logSuccess('Fail reason added');
                    this._site.shoutFeedback();
                }
            );
        }
    }
}



