import {Component, OnInit, Input} from '@angular/core';
import {CustomSelectComponent} from '../custom-select.component';
import {SiteService} from '../../../services/site/site.service';

@Component({
    selector: '[select-add-asset-fail-reason]',
    templateUrl: 'select-add-asset-fail-reason.component.html'
})

export class SelectAddAssetFailReasonComponent extends CustomSelectComponent implements OnInit {

    @Input() assetType: string = null;
    @Input() feedback: string = null;

    constructor(private _site: SiteService) {
        super();
    }

    /**
     * Pull asset fail reasons and update options
     */
    ngOnInit() {
        this._site.getReasonsByAssetType(this.assetType).subscribe(
            res => {
                const reasons = res[this.assetType].map(reason => {
                    return {label: reason, value: reason};
                });
                this.options = [{label: 'No Reason', value: ''}].concat(reasons);
            }
        );
    }
}



