import {Component, ElementRef, ViewChild} from '@angular/core';
import {ModalComponent} from '../modal.component';
import {ModalService} from '../../../services/modal/modal.service';
import {DatasetService} from '../../../services/dataset/dataset.service';

@Component({
    selector: '[modal-upload-dataset]',
    templateUrl: 'modal-upload-dataset.component.html'
})
export class ModalUploadDatasetComponent extends ModalComponent {

    file: File = null;
    fileFeedback: string = null;
    allowedFileTypes: Array<string> = [
        'text/csv',
        'application/vnd.ms-excel'
    ];

    uploadingFile: boolean = false;
    draggingOver: boolean = false;

    @ViewChild('datasetFile') datasetFile: ElementRef;

    constructor(public _modal: ModalService,
                private _dset: DatasetService) {
        super(_modal);
    }

    /**
     * Check if client can use HTML5 FileReader
     * to read files
     * @returns {boolean}
     */
    checkCanReadFiles(): boolean {
         return !!(File && FileReader && FileList && Blob);
    }

    /**
     * Handle file adding event
     * @param event
     */
    fileAdded(event: Event) {
        const file = (<HTMLInputElement>event.target).files[0];
        if (file && this.checkCanReadFiles()) {
            const reader = new FileReader();
            reader.onload = (e: any) => {
                if (this.allowedFileTypes.indexOf(file.type) < 0) {
                    this.fileFeedback = 'Wrong file format';
                    return;
                }
                this.file = file;
                this.fileFeedback = file.name;
            };
            reader.readAsText(file);
        } else {
            this.fileFeedback = 'Failed to load file';
        }
    }

    /**
     * Submit uploaded file to the server
     */
    submitFile(): void {
        this.uploadingFile = true;
        const data = new FormData();
        data.append('source', this.file);
        this._dset.uploadDataset(data).subscribe(
            res => {
                this.file = null;
                this.fileFeedback = 'Data set uploaded!';
            },
            err => {},
            () => {
                setTimeout(() => {
                    this.uploadingFile = false;
                }, 1000);
            }
        );
    }

    /**
     * Show drop zone drag over styling
     */
    showDragOver(): void {
        this.draggingOver = true;
    }

    /**
     * Hide drop zone drag over styling
     */
    hideDragOver(): void {
        this.draggingOver = false;
    }
}



