import {Component, OnInit} from '@angular/core';
import {ScraperService} from '../../../services/scraper/scraper.service';
import {Scraper} from '../../../interfaces/scraper';
import {BaseComponent} from '../../base/base.component';

@Component({
    selector: 'scrapers',
    templateUrl: 'scrapers.component.html'
})
export class ScrapersComponent extends BaseComponent implements OnInit {

    scrapers: Array<Scraper> = [];

    selectedForComparison: Array<any> = [];

    showFilters: boolean = false;

    /**
     * sorting get/set
     * @type {string}
     * @private
     */
    private _sorting: string = '-version';

    get sorting(): string {
        return this._sorting;
    }

    set sorting(value: string) {
        this._sorting = value;
        this.getScrapers();
    }

    /**
     * currentDataset get/set
     * @type {number}
     * @private
     */
    private _currentDataset: number = 0;

    get currentDataset(): number {
        return this._currentDataset;
    }

    set currentDataset(value: number) {
        this._currentDataset = value;
        this.getScrapers();
    }

    constructor(private _scrap: ScraperService) {
        super();
    }

    /**
     * Get all scrapers
     * Subscribe to scrapers selection
     */
    ngOnInit() {
        this.getScrapers();
        this._scrap.selectedForComparison.takeWhile(() => this.isAlive).subscribe(
            res => {
                this.selectedForComparison = res;
            }
        );
    }

    /**
     * Get scrapers as per current params
     */
    getScrapers(): void {
        const args = {
            sorting: this.sorting,
            dataset: this.currentDataset
        };
        this._scrap.getScrapers(args).subscribe(
            res => {
                this.scrapers = res.scrapers;
            }
        );
    }

    /**
     * Clear scrapers selection
     */
    clearSelected(): void {
        this._scrap.clearSelection();
    }

    /**
     * Toggle filters on/off
     */
    toggleFilters(): void {
        this.showFilters = !this.showFilters;
    }
}



