import {Component, OnInit} from '@angular/core';
import {ScraperService} from '../../../../services/scraper/scraper.service';
import {ActivatedRoute} from '@angular/router';
import {Scraper} from '../../../../interfaces/scraper';

@Component({
    selector: 'scraper-statistic',
    templateUrl: 'scraper-statistic.component.html'
})
export class ScraperStatisticComponent implements OnInit {

    scraperVersion: number;
    scraper: Scraper;

    /**
     * currentDataset get/set
     * @type {number}
     * @private
     */
    private _currentDataset: number = 0;

    get currentDataset(): number {
        return this._currentDataset;
    }

    set currentDataset(value: number) {
        this._currentDataset = value;
        this.getScraperInfo();
    }

    constructor(private _scrap: ScraperService,
                private _ar: ActivatedRoute) {
    }

    /**
     * Get scraper by version from route param
     */
    ngOnInit() {
        this._ar.params.subscribe(params => {
            this.scraperVersion = params['version'];
            this.getScraperInfo();
        });
    }

    /**
     * Get scraper info
     */
    getScraperInfo() {
        this._scrap.getScraperByVersion(this.scraperVersion, this.currentDataset).subscribe(
            res => {
                this.scraper = res;
            }
        );

    }
}



