import {Component, Input, OnInit, Output, EventEmitter} from '@angular/core';
import {INgxMyDpOptions, IMyDateModel} from 'ngx-mydatepicker';
import * as moment from 'moment';

@Component({
    selector: '[datepicker]',
    templateUrl: 'datepicker.component.html'
})
export class DatepickerComponent implements OnInit {

    @Input() initDate: any;
    @Output() date: EventEmitter<any> = new EventEmitter<any>();

    options: INgxMyDpOptions = {
        dateFormat: 'mmm dd yyyy',
        showTodayBtn: false,
        showSelectorArrow: false,
        selectorHeight: '270px',
        monthSelector: false,
        yearSelector: false
    };

    constructor() {
    }

    /**
     * Set initial picker value
     */
    ngOnInit(): void {
        if (!this.initDate || !moment(new Date(this.initDate)).isValid()) {
            const today = new Date();
            this.initDate = {
                date: {
                    year: today.getFullYear(),
                    month: today.getMonth() + 1,
                    day: today.getDate()
                }
            };
        } else {
            const date = new Date(this.initDate);
            this.initDate = {
                date: {
                    year: date.getFullYear(),
                    month: date.getMonth() + 1,
                    day: date.getDate()
                }
            };
        }
    }

    /**
     * Output chosen value
     * @param date
     */
    onDateChanged(date: IMyDateModel): void {
        this.date.emit(date);
    }
}



