import {Component, OnInit, Input} from '@angular/core';
import {BusinessService} from '../../../../services/business/business.service';
import {Business} from '../../../../interfaces/business';
import {BaseComponent} from '../../../base/base.component';

@Component({
    selector: '[businesses-table-row]',
    templateUrl: 'businesses-table-row.component.html'
})
export class BusinessesTableRowComponent extends BaseComponent implements OnInit {

    @Input() business: Business;

    isChecked: boolean = false;

    constructor(private _busin: BusinessService) {
        super();
    }

    /**
     * Subscribe to selection list
     */
    ngOnInit() {
        this._busin.selectedForScraping.takeWhile(() => this.isAlive).subscribe(
            res => {
                this.isChecked = res.indexOf(this.business.id) > -1;
            }
        );
    }

    /**
     * Toggle item selection
     */
    toggleChecked(): void {
        this.isChecked ? this._busin.removeSelected(this.business.id) : this._busin.addSelected(this.business.id);
    }
}



