import {Component, OnInit, Input, AfterViewInit, ChangeDetectorRef} from '@angular/core';
import {ScraperService} from '../../../../services/scraper/scraper.service';
declare const $;

@Component({
    selector: '[fail-list-item]',
    templateUrl: 'fail-list-item.component.html'
})
export class FailListItemComponent implements OnInit, AfterViewInit {

    @Input() failItem: any = null;
    @Input() scraperVersion: number;
    @Input() slug: string;

    failReasons: Array<any> = [];
    loadingReasons: boolean = true;
    reasonsFetchedUp: boolean = false;

    constructor(private _scrap: ScraperService,
                private _cdRef: ChangeDetectorRef) {
    }

    ngOnInit() {
    }

    ngAfterViewInit(): void {
        $(`#${this.failItem.kind}-${this.slug}`).on('show.bs.collapse', () => {
            if (!this.reasonsFetchedUp) {
                this.getReasons();
            }
        });
    }

    getReasons(): void {
        this._scrap.getFailReasonsByKind(this.scraperVersion, this.failItem.kind).subscribe(
            res => {
                console.log(res);
                this.failReasons = res.reasons;
                this.reasonsFetchedUp = true;
            },
            err => {},
            () => {
                this.loadingReasons = false;
                this._cdRef.detectChanges();
            }
        );
    }
}



