import {Component, OnInit, EventEmitter, Input, Output} from '@angular/core';
import {FormControl} from '@angular/forms';
import 'rxjs/add/operator/debounceTime';

@Component({
    selector: '[dynamic-search]',
    templateUrl: 'dynamic-search.component.html'
})

export class DynamicSearchComponent implements OnInit {

    @Input() placeholder: string = 'Search';

    @Output() searchQuery: EventEmitter<string> = new EventEmitter<string>();
    searchQueryControl: FormControl = new FormControl();

    /**
     * Subscribe to search input changes and output value
     */
    ngOnInit() {
        this.searchQueryControl.valueChanges.debounceTime(1000).subscribe(
            res => {
                this.emitQueryValue(res);
            }
        );
    }

    /**
     * Output search query value
     * @param query
     */
    emitQueryValue(query: string): void {
        this.searchQuery.emit(query);
    }
}



