import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'siteUrl'
})
export class SiteUrlPipe implements PipeTransform {

    transform(value: string): string {
        if (value.indexOf('://') > -1) {
            return value.substring(value.indexOf('://') + 3);
        }
        return value;
    }
}



