import {Component} from '@angular/core';
import {CustomSelectComponent} from '../../../components/custom-select/custom-select.component';

@Component({
    selector: '[select-failed-asset]',
    templateUrl: 'select-failed-asset.component.html'
})
export class SelectFailedAssetComponent extends CustomSelectComponent {

    options: Array<any> = [
        { label: 'Failed Asset', value: ''},
        { label: 'Company Name', value: 'name'},
        { label: 'Slogan', value: 'slogan'},
        { label: 'Phone', value: 'phone'},
        { label: 'Colors', value: 'colors'},
        { label: 'Font', value: 'font'},
        { label: 'Logo', value: 'logo'},
        { label: 'Images', value: 'backgrounds'}
    ];

    constructor() {
        super();
    }
}



