import {Injectable} from '@angular/core';
import {ApiService} from '../api/api.service';
import {Observable} from 'rxjs/Observable';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class ScraperService {

    selectedForComparison: BehaviorSubject<Array<any>> = new BehaviorSubject<Array<any>>([]);

    constructor(private _api: ApiService) {
    }

    /**
     * Get scrapers selected for comparison
     * @returns {Array<any>}
     */
    getSelected(): Array<any> {
        return this.selectedForComparison.getValue();
    }

    /**
     * Add selected scraper to selected list
     * @param version
     */
    addSelected(version: number): void {
        if (this.getSelected().length < 2) {
            this.selectedForComparison.next(this.getSelected().concat(version));
        }
    }

    /**
     * Remove selected scraper from selected list
     * @param version
     */
    removeSelected(version: number): void {
        const selected = this.getSelected();
        const index = selected.indexOf(version);
        if (index > -1) {
            selected.splice(index, 1);
            this.selectedForComparison.next(selected);
        }
    }

    /**
     * Clear selected scrapers info
     */
    clearSelection(): void {
        this.selectedForComparison.next([]);
    }

    /**
     * Get all scrapers
     * @param args
     * @returns {Observable<R>}
     */
    getScrapers(args?: any): Observable<any> {
        const reqUrl = `scrapers${`?sort=${args ? args.sorting : ''}&dataset_id=${args && args.dataset ? args.dataset : ''}`}`;
        return this._api.get(reqUrl);
    }

    /**
     * Get scraper by version
     * @param version
     * @returns {Observable<R>}
     */
    getScraperByVersion(version: string | number, dataset?: number): Observable<any> {
        return this._api.get(`scrapers/${version}?dataset_id=${dataset || ''}`);
    }

    /**
     * Get scraper fail reasons by kind of reason
     * @param version
     * @param kind
     * @returns {Observable<R>}
     */
    getFailReasonsByKind(version: number, kind: string): Observable<any> {
        return this._api.get(`scrapers/${version}/reasons/${kind}`);
    }
}



