import {Component, Input, Output, EventEmitter} from '@angular/core';

@Component({
    selector: '[asset-fail-reason-filter]',
    templateUrl: 'asset-fail-reason-filter.component.html'
})
export class AssetFailReasonFilterComponent {

    @Input() index: number = null;
    @Output() value: EventEmitter<Object> = new EventEmitter<Object>();
    @Output() remove: EventEmitter<any> = new EventEmitter<any>();

    private _assetType: string = '';
    private _assetFailReason: string = '';

    /**
     * assetType getter
     * @returns {string}
     */
    get assetType(): string {
        return this._assetType;
    }

    /**
     * assetType setter
     * Emit falsy value
     * @param value
     */
    set assetType(value: string) {
        this._assetType = value;
        this.emitFalsy();
    }

    /**
     * assetFailReason getter
     * @returns {string}
     */
    get assetFailReason(): string {
        return this._assetFailReason;
    }

    /**
     * assetFailReason setter
     * @param value
     */
    set assetFailReason(value: string) {
        this._assetFailReason = value;
        this.assetFailReason ? this.emitTruthy() : this.emitFalsy();
    }

    /**
     * Emit delete event
     */
    removeThisPicker() {
        this.remove.emit();
    }

    /**
     * Emit currently set value
     */
    emitTruthy() {
        this.value.emit({
            assetType: this.assetType,
            assetFailReason: this.assetFailReason
        });
    }

    /**
     * Emit empty value
     */
    emitFalsy() {
        this.value.emit({
            assetType: '',
            assetFailReason: ''
        });
    }
}



